<template>
  <div class="header">
    <div class="header-container">
      <img src="../assets/home/LOGO.png" class="logo" alt />
      <div>
        <ul class="header-bar">
          <li :class="active==1?'active':''" class="font-hover" @click="gohome()">
            首页
            <div class="circle" :style="active==1?'background: #4af6cd;':''"></div>
          </li>
          <li :class="active==2?'active':''" @mouseenter="enter()" @mouseleave="leave()" class="prod-container font-hover">
            产品
            <div class="circle" :style="active==2?'background: #4af6cd;':''"></div>
            <div class="prod-list" :style="isShow?'display:block':''">
              <div :class="prodActive==1?'prod-active':''" @click="goprod()" class="font-hover">发型纪</div>
              <div :class="prodActive==2?'prod-active':''" @click="goprodAssistant()" class="font-hover">发型纪助手</div>
              <div :class="prodActive==3?'prod-active':''" @click="goprodMerchants()" class="font-hover">发型纪商家版</div>
            </div>
          </li>
          <li :class="active==3?'active':''" @click="gosuccess()" class="font-hover">
            成功案例
            <div class="circle" :style="active==3?'background: #4af6cd;':''"></div>
          </li>
          <li :class="active==4?'active':''" @click="goaboutus()" class="font-hover">
            关于我们
            <div class="circle" :style="active==4?'background: #4af6cd;':''"></div>
          </li>
        </ul>
      </div>
    </div>
    <!-- <div class="header-line"></div> -->
  </div>
</template>
<script>
export default {
  data() {
    return {
      active: 0,
      prodActive:0,
      isShow:false
    };
  },
  created() {
    this.getRouter();
  },
  methods: {
    leave(){
      this.isShow=false
    },
    enter(){
      this.isShow=true
    },
    gosuccess() {
      this.$router.push("/success");
    },
    goaboutus() {
      this.$router.push("/aboutus");
    },
    gohome() {
      this.$router.push("/");
    },
    goprod() {
      this.$router.push("/prod");
    },
    goprodAssistant() {
      this.$router.push("/prodAssistant");
    },
    goprodMerchants() {
      this.$router.push("/prodMerchants");
    },
    getRouter() {
      if (this.$route.name == "home") {
        this.active = 1;
      } else if (this.$route.name == "success") {
        this.active = 3;
      } else if (this.$route.name == "aboutus") {
        this.active = 4;
      } else {
        this.active = 2;
        if (this.$route.name == "prod") {
          this.prodActive=1
        } else if (this.$route.name == "prodAssistant") {
          this.prodActive=2

        } else if (this.$route.name == "prodMerchants") {
          this.prodActive=3
        }
      }
    }
  }
};
</script>
